"use client"

import { useState } from "react"

export default function Header() {
    const [open, setOpen] = useState(false)

    return (
        <header className="flex p-4 bg-gray-800 text-white justify-around relative">

            <strong>Логотип</strong>

            <nav className="gap-4 flex items-center justify-center">
                <a href="/">Главная</a>
                <a href="/">О нас</a>
                <a href="/">Услуги</a>
            </nav>


            <div>
                <button onClick={() => setOpen(!open)}>Вход</button>
            </div>


            {open && 
            <div 
            className="absolute top-full right-[20%] bg-white text-black p-4 transition-all duration-300
                        mt-2 border rounded shadow-lg flex flex-col gap-4">
                <span>Войти с помощью</span>
                <button onClick={() => {}}>GitHub</button>
                <button onClick={() => {}}>Google</button>
            </div>
            }
        </header>
    )
}